<template>
  <div class="page-cells_wrapper">
    <div class="weui-cells page-cells-a">
      <template v-for="input in inputs">
        <text-ele @msg="updateJobInfo" :inputInfo="input" v-if="input.type == 'text'"></text-ele>
        <select-ele @msg="updateJobInfo" :inputInfo="input" :jobData="jobData[input.name]" v-if="input.type == 'select'"></select-ele>
        <textarea-ele @msg="updateJobInfo" :inputInfo="input" v-if="input.type == 'textarea'"></textarea-ele>
      </template>
      <div class="page-btn-bg">
        <a href="javascript:;" @click="save" class="weui-btn weui-btn_primary"></a>
      </div>
      <!--<v-dialog></v-dialog>-->
    </div>
  </div>
</template>

<script>
  import { ajax } from 'common';
  import TextEle from './TextEle';
  import TextareaEle from './TextareaEle';
  import SelectEle from './SelectEle';
  export default {
    data() {
      return {
        url: `${window.AppConf.apiHost}/hr/job`,
        jobInfo: {},
        inputs: [{
          'type': 'select',
          'name': 'category',
          'title': '',
          'placeholder': ''
        }, {
          'type': 'text',
          'name': 'name',
          'title': '',
          'placeholder': ''
        }, {
          'type': 'select',
          'name': 'nature',
          'title': '',
          'placeholder': ''
        }, {
          'type': 'select',
          'name': 'experience',
          'title': '',
          'placeholder': ''
        }, {
          'type': 'select',
          'name': 'educational',
          'title': '',
          'placeholder': ''
        }, {
          'type': 'select',
          'name': 'salary',
          'title': '',
          'placeholder': ''
        }, {
          'type': 'textarea',
          'name': 'describle',
          'title': '',
          'placeholder': ''
        }, {
          'type': 'text',
          'name': 'address',
          'title': '',
          'placeholder': ''
        }],
        jobData: {
          'category': {
            '0001': ''
          },
          'nature': {
            '0001': '',
            '0002': '',
            '0003': '',
            '0004': ''
          },
          'experience': {
            '0001': '',
            '0002': '',
            '0003': '3',
            '0004': '3-5',
            '0005': '5-10',
            '0006': '10',
            '0007': ''
          },
          'educational': {
            '0001': '',
            '0002': '',
            '0003': '',
            '0004': '',
            '0005': '',
            '0006': ''
          },
          'salary': {
            '0001': '2K',
            '0002': '2K-5K',
            '0003': '5K-10K',
            '0004': '10K-15K',
            '0005': '15K-25K',
            '0006': '25K-50K',
            '0007': '50K'
          }
        }
      }
    },
    methods: {
      save() {
        if (this.validate()) {
          ajax.post(this.url, this.jobInfo).then((res) => {
            if (res.status) {
              // store
              // 
              this.$router.push('/hr/job/preview');
            }
          });
        }
      },
      updateJobInfo(info) {
        Object.assign(this.jobInfo, info);
      },
      validate() {
        /**
         * 
         * 1. 
         * 2. 
         */
        if (Object.keys(this.jobInfo).length === this.inputs.length) {
          let isOk = true;
          this.inputs.forEach((input) => {
            if ((input.type === 'text' || input.type === 'textarea') && !(this.jobInfo[input.name])) {
              isOk = false;
              this.$toast.msg(`${input.title}`);
            } else if (input.type === 'select' && !(this.jobData[input.name][this.jobInfo[input.name]])) {
              isOk = false;
              this.$toast.msg(`${input.title}`);
            }
          })
          return isOk;
        } else {
          this.$toast.msg('');
          return false;
        }
      }
    },
    components: {
      TextEle,
      TextareaEle,
      SelectEle
    }
  }
</script>

<style lang="scss">
  .page {
    &-cells {
      &-a {
        margin-top: 0;
        background: transparent;
      }
      &_wrapper {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 50px;
        width: 100%;
        overflow: auto;
        -webkit-overflow-scrolling: touch;
      }
    }
    &-cell {
      &-a {
        padding: 2px 0;
        height: 40px;
        line-height: 40px;
        text-align: center;
        margin: 10px 0;
        background: #fff;

        &:first-child {
          margin-top: 0;
        }
      }
      &__bd_a {
        padding-left: 15px;
      }
    }

    &-btn {

      &-bg {
        background: #fff;
        padding: 10px;
      }
    }
  }
</style>



